// ========================================================
// Dependencies
// ========================================================
@import '~@Green-Dot-Corporation/eureka-lib-style-utils/src/styles/mixins/animations.scss';
@import '~@Green-Dot-Corporation/eureka-lib-style-utils/src/styles/mixins/layout.scss';
@import '~@Green-Dot-Corporation/eureka-lib-style-utils/src/styles/mixins/resets.scss';
@import '~@Green-Dot-Corporation/eureka-lib-style-utils/src/styles/utils/unit-conversion-utils.scss';
@import '~@Green-Dot-Corporation/eureka-lib-theme-utils/src/styles/mixins/states.scss';
@import '~@Green-Dot-Corporation/eureka-lib-theme-utils/src/styles/mixins/typography.scss';
@import '~@Green-Dot-Corporation/eureka-lib-theme-utils/src/styles/utils/theme-utils.scss';
@import './mixins.scss';

.eureka-button, %eureka-button {
    @include eureka-large-text;
    @include eureka-button-reset;
    @include eureka-focus-state-halo;
    @include eureka-hover-state;
    @include eureka-disabled-state;
    $this: '.eureka-button';
    color: inherit;
    background-color: var(--eureka-core-color-canvas, eureka-light-theme('core-color-canvas'));
    box-sizing: border-box;
    border: var(--eureka-btn-border-width, eureka-light-theme('btn-border-width')) solid transparent;
    border-radius: var(--eureka-btn-border-radius, eureka-light-theme('btn-border-radius'));

    &__content-container {
        display: inline-block;
        padding: #{eureka-to-rem(eureka-light-theme('core-system-scale'))} #{eureka-to-rem(eureka-light-theme('core-system-scale') * 2)};
    }

    &__text {
        font-weight: var(--eureka-btn-font-weight, eureka-light-theme('btn-font-weight'));
    }

    &__text-container {
        line-height: var(--eureka-core-typography-line-height, eureka-light-theme('core-typography-line-height'));
        vertical-align: middle;
    }

    &__icon-container {
        vertical-align: middle;
    }

    &__text-container,
    &__icon-container {
        box-sizing: border-box;
        user-select: none;
        display: inline-block;
        padding: #{eureka-to-rem(eureka-light-theme('core-system-scale'))};
    }

    &__icon-container + &__text-container {
        vertical-align: middle;
    }

    &__icon-safe-space {
        @include eureka-flex-row;
        width: eureka-light-theme('btn-icon-font-size');
        vertical-align: middle;
        box-sizing: border-box;
    }

    &__icon {
        @include eureka-flex-row;
        vertical-align: middle;
        font-size: eureka-light-theme('btn-icon-font-size');
        margin: auto;
        flex: 0 0 auto;

        .eureka-icon {
            margin: auto;
        }
    }

    #{$this}__busy-icon {
        font-size: 1em;

        .eureka-loading-dots__dot {
            background-color: currentColor;
        }
    }

    &--primary {
        color: var(--eureka-btn-primary-color-text, eureka-light-theme('btn-primary-color-text'));
        background-color: var(--eureka-btn-primary-color-bg, eureka-light-theme('btn-primary-color-bg'));
        border-color: var(--eureka-btn-primary-color-border, eureka-light-theme('btn-primary-color-border'));

        &:focus-visible {
            @include eureka-button-primary-focus-visible;
        }

        @media (hover: hover) {
            &:enabled:hover {
                @include eureka-button-primary-hover;
            }
        }
    }

    &--secondary {
        color: var(--eureka-btn-secondary-color-text, eureka-light-theme('btn-secondary-color-text'));
        background-color: var(--eureka-btn-secondary-color-bg, eureka-light-theme('btn-secondary-color-bg'));
        border-color: var(--eureka-btn-secondary-color-border, eureka-light-theme('btn-secondary-color-border'));

        &:focus-visible {
            @include eureka-button-secondary-focus-visible;
        }

        @media (hover: hover) {
            &:enabled:hover {
                @include eureka-button-secondary-hover;
            }
        }
    }

    &--primary-destructive {
        color: var(--eureka-btn-primary-destructive-color-text, eureka-light-theme('btn-primary-destructive-color-text'));
        background-color: var(--eureka-btn-primary-destructive-color-bg, eureka-light-theme('btn-primary-destructive-color-bg'));
        border-color: var(--eureka-btn-primary-destructive-color-border, eureka-light-theme('btn-primary-destructive-color-border'));

        &:focus-visible {
            @include eureka-button-destructive-focus-visible;
        }

        @media (hover: hover) {
            &:enabled:hover {
                @include eureka-button-primary-destructive-hover;
            }
        }
    }

    &--secondary-destructive {
        color: var(--eureka-btn-secondary-destructive-color-text, eureka-light-theme('btn-secondary-destructive-color-text'));
        background-color: var(--eureka-btn-secondary-destructive-color-bg, eureka-light-theme('btn-secondary-destructive-color-bg'));
        border-color: var(--eureka-btn-secondary-destructive-color-border, eureka-light-theme('btn-secondary-destructive-color-border'));

        &:focus-visible {
            @include eureka-button-destructive-focus-visible;
        }

        @media (hover: hover) {
            &:enabled:hover {
                @include eureka-button-secondary-destructive-hover;
            }
        }
    }

    // stylelint-disable no-descending-specificity
    &--primary,
    &--secondary,
    &--destructive,
    &--primary-destructive,
    &--secondary-destructive {
        &:active:enabled {
            @include eureka-button-active;
        }
    }

    &--busy {
        .eureka-icon-spinner {
            @include eureka-rotate-infinite;
        }

        .eureka-icon-spinner::before {
            transform: translate3d(0.1px, -0.2px, 0);
        }
    }
}
